ng:0.1em;text-align:center}.macro-pie-container #pie-chart{font-family:'SmoothStone';letter-spacing:0.1em}#micro-bar-container{grid-column-start:11;grid-column-end:18;grid-row:2;text-align:center;padding-top:62px;margin:auto}#bar-char{padding:0}.bar{fill:#80cbc4}line#limit{stroke:#FED966;stroke-width:2;stroke-dasharray:3}.grid path{stroke-width:0}.grid .tick line{stroke:#9FAAAE;stroke-opacity:0.3}text.divergence{font-size:14px;fill:red;font-weight:600;letter-spacing:0.05em;font-family:Arial, Helvetica, sans-serif}text.value{font-size:14px;font-weight:600;fill:purple;letter-spacing:0.05em;font-family:Arial, Helvetica, sans-serif}#bar-chart .micro-categories,.micro-title{font-size:40px;letter-spacing:5px}#bar-chart .label-grams,.label-milligrams{font-size:26px;letter-spacing:5px}.nutrition-facts-div{grid-column-start:1;grid-column-end:6;grid-row:2;width:300px;margin:auto}.nutrition-facts-container{line-height:1.3;padding:10px;border:1px solid black}.nutrition-facts-title{font-weight:bold;font-size:30px;border-bottom:8px solid black;width:99%;margin-bottom:10px;padding-bottom:10px}.nutrition-facts-table table{margin:0}.small-info{font-weight:normal;font-size:11px;padding-bottom:3px;width:270px;text-align:left}.daily-values{font-weight:normal;font-size:11px;border-top:1px solid black;width:270px;margin:5px 5px 0;padding:5px 0 0}.daily-total{font-size:11px;border-top:4px solid black;width:270px;margin:5px 5px 0;padding:5px 0 3px 183px}#total-calories,#total-fat,#total-saturated-fat,#total-cholesterol,#total-sodium,#total-potassium,#total-carbohydrate-fiber,#total-dietary-fiber,#total-sugar,#total-protein,#total-calcium,#total-iron,#total-vitamin-C,#total-vitamin-A{font-weight:normal;float:right}.each-title{border-top:1px solid black;padding:4px 0 2px;font-size:12px;font-weight:normal;padding-left:20px;text-align:left}.each-title-bold{border-top:1px solid black;padding:4px 0 2px;font-size:12px;text-align:left}
